@charset "UTF-8";
@import url('https://fonts.googleapis.com/css2?family=Ubuntu+Sans+Mono:ital,wght@0,400..700;1,400..700&display=swap');

body {
  color: #e3e7e9;
  background-color: rgb(23, 22, 23);
  font-family: "Ubuntu Sans Mono", monospace;
  width: 90%;
  max-width: 1000px;
  margin: 5px auto;
  padding: 10px;
  text-align: left;
}

a {
  color: #7C9EB2;
}

p {
  text-indent: 3px;

  margin-bottom: 10px;
}

nav {
  text-align: left;
  size: 15px;
  border-bottom: 2px dashed #4b5f6b;
  border-top: 2px dashed #4b5f6b;
  padding: 10px;
}

h1 {
  color: #d7e1e7;
  size: 10px;
  margin-bottom: 10px;
  margin-top: 10px;
}


h5 {
  color: #c3ccd1;
  size: 10px;
  text-indent: 3px;
  margin-bottom: 10px;
  margin-top: 10px;
}

h2 {
  color: #a3b6c2;
  size: 10px;
  margin-bottom: 10px;
  margin-top: 10px;
}

hr {
  border-width: 1px;
  color: #4b5f6b;
  border-style: dashed;
}

.imglarge{
  width: 60%;
  height: auto;
}

ul {
  list-style-type: none;
}

li {
  list-style-type: none;
}

footer {
  text-align: right;
  color: #4b5f6b;
}